<template>
  <div class="kol-detail-info">
    <div class="info-card">
      <div class="card-header">
        <div class="author-info">
          <div class="author-avatar">
            {{ kol.user_tag.charAt(0).toUpperCase() }}
          </div>
          <div class="author-details">
            <span class="author-tag">@{{ kol.user_tag }}</span>
            <span class="publish-date">{{ formatDate(kol.created_at) }}</span>
          </div>
        </div>
        <div class="signal-stats">
          <div class="stat">
            <i class="fas fa-eye"></i>
            <span>{{ kol.bought_num }} purchases</span>
          </div>
        </div>
      </div>

      <div class="card-content">
        <h1 class="signal-title">{{ kol.title }}</h1>
        
        <div class="metrics-row">
          <div class="metric-card">
            <i class="fas fa-chart-line"></i>
            <div class="metric-content">
              <span class="metric-label">Potential Growth</span>
              <span class="metric-value">+{{ kol.potential }}%</span>
            </div>
          </div>
          
          <div class="metric-card">
            <i class="fas fa-thumbs-up"></i>
            <div class="metric-content">
              <span class="metric-label">Positive Rating</span>
              <span class="metric-value">{{ likeRatio }}%</span>
            </div>
          </div>
        </div>

        <div class="description-section">
          <h3>Analysis & Prediction</h3>
          <p class="description-text">{{ kol.description }}</p>
        </div>

        <div v-if="hasAccess" class="private-content">
          <div class="private-header">
            <i class="fas fa-lock-open"></i>
            <h3>Token Information</h3>
          </div>
          
          <div class="token-info">
            <div class="token-field">
              <label>Token Symbol:</label>
              <span class="token-value">{{ kol.symbol }}</span>
            </div>
            
            <div class="token-field">
              <label>Contract Address:</label>
              <div class="contract-display">
                <span class="contract-text">{{ shortenAddress(kol.mint) }}</span>
                <button @click="copyToClipboard(kol.mint)" class="copy-btn">
                  <i class="fas fa-copy"></i>
                </button>
              </div>
            </div>
          </div>
        </div>

        <div v-else class="locked-content">
          <div class="lock-icon">
            <i class="fas fa-lock"></i>
          </div>
          <h3>Token Information</h3>
          <p>Purchase this signal to reveal token details and trading information.</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'

export default {
  name: 'KolDetailInfo',
  props: {
    kol: {
      type: Object,
      required: true
    },
    hasAccess: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const likeRatio = computed(() => {
      const total = props.kol.likes + props.kol.unlikes
      if (total === 0) return 0
      return Math.round((props.kol.likes / total) * 100)
    })

    const formatDate = (dateString) => {
      const date = new Date(dateString)
      const now = new Date()
      const diffTime = now - date
      const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24))
      
      if (diffDays === 0) {
        const diffHours = Math.floor(diffTime / (1000 * 60 * 60))
        if (diffHours === 0) {
          const diffMinutes = Math.floor(diffTime / (1000 * 60))
          return `${diffMinutes} min ago`
        }
        return `${diffHours} h ago`
      }
      
      if (diffDays === 1) return 'yesterday'
      if (diffDays < 7) return `${diffDays} days ago`
      
      return date.toLocaleDateString('en-US', {
        day: 'numeric',
        month: 'short',
        year: date.getFullYear() !== now.getFullYear() ? 'numeric' : undefined
      })
    }

    const shortenAddress = (address) => {
      if (!address) return ''
      return `${address.slice(0, 6)}...${address.slice(-6)}`
    }

    const copyToClipboard = async (text) => {
      try {
        await navigator.clipboard.writeText(text)
        alert('Address copied to clipboard!')
      } catch (err) {
        console.error('Failed to copy:', err)
      }
    }

    return {
      likeRatio,
      formatDate,
      shortenAddress,
      copyToClipboard
    }
  }
}
</script>

