<template>
  <div class="kol-detail-page">
    <div class="container">
      <div v-if="loading" class="loading-container">
        <div class="loading-spinner"></div>
        <p>Loading signal details...</p>
      </div>

      <div v-else-if="kol" class="detail-content">
        <div class="back-nav">
          <button @click="goBack" class="back-btn">
            <i class="fas fa-arrow-left"></i>
            Back to Signals
          </button>
        </div>

        <div class="detail-grid">
          <div class="main-content">
            <KolDetailInfo 
              :kol="kol"
              :has-access="hasAccess"
            />
          </div>

          <div class="sidebar">
            <KolPurchaseCard 
              :kol="kol"
              :has-access="hasAccess"
              @purchase="handlePurchase"
            />
          </div>
        </div>
      </div>

      <div v-else class="error-container">
        <i class="fas fa-exclamation-triangle"></i>
        <h3>Signal Not Found</h3>
        <p>The requested KOL signal could not be found.</p>
        <button @click="goBack" class="back-btn">
          Back to Signals
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import store from '@/vuex'
import KolDetailInfo from '@/components/kol/KolDetailInfo.vue'
import KolPurchaseCard from '@/components/kol/KolPurchaseCard.vue'
import axios from 'axios'

export default {
  name: 'KolDetailPage',
  components: {
    KolDetailInfo,
    KolPurchaseCard
  },
  setup() {
    const route = useRoute()
    const router = useRouter()
    
    const kol = ref(null)
    const loading = ref(false)

    const kolId = computed(() => route.params.kolId)
    const hasAccess = computed(() => kol.value?.hasAccess || false)
    const isAuthenticated = computed(() => store.getters.isAuthenticated)

    const loadKol = async () => {
      loading.value = true
      try {
        const response = await axios.get(`/kol/${kolId.value}`)
        if (response.data.success) {
          kol.value = response.data.data
        }
      } catch (error) {
        console.error('Error loading KOL:', error)
      } finally {
        loading.value = false
      }
    }

    const goBack = () => {
      router.push('/kol')
    }

    const handlePurchase = () => {
      if (!isAuthenticated.value) {
        alert('Please connect your wallet to purchase')
        return
      }
      alert('Purchase functionality will be implemented')
    }

    onMounted(() => {
      loadKol()
    })

    return {
      kol,
      loading,
      hasAccess,
      goBack,
      handlePurchase
    }
  }
}
</script>

