<template>
  <div class="purchase-card">
    <div class="card-header">
      <h3>Signal Access</h3>
      <div class="price-display" :class="priceClass">
        {{ formatPrice(kol.price) }}
      </div>
    </div>

    <div class="card-content">
      <div v-if="hasAccess" class="access-granted">
        <div class="success-icon">
          <i class="fas fa-check-circle"></i>
        </div>
        <h4>Access Granted</h4>
        <p>You have full access to this signal</p>
      </div>

      <div v-else class="purchase-section">
        <div class="benefits-list">
          <div class="benefit-item">
            <i class="fas fa-unlock"></i>
            <span>Token symbol & contract</span>
          </div>
          <div class="benefit-item">
            <i class="fas fa-chart-line"></i>
            <span>Full trading analysis</span>
          </div>
          <div class="benefit-item">
            <i class="fas fa-thumbs-up"></i>
            <span>Reaction & feedback access</span>
          </div>
        </div>

        <button 
          v-if="kol.price > 0"
          @click="$emit('purchase')" 
          class="purchase-btn"
        >
          <i class="fas fa-shopping-cart"></i>
          Purchase Access
        </button>

        <button 
          v-else
          @click="$emit('purchase')" 
          class="free-access-btn"
        >
          <i class="fas fa-gift"></i>
          Get Free Access
        </button>
      </div>

      <div class="signal-stats">
        <div class="stat-item">
          <span class="stat-label">Purchases</span>
          <span class="stat-value">{{ kol.bought_num }}</span>
        </div>
        <div class="stat-item">
          <span class="stat-label">Likes</span>
          <span class="stat-value">{{ kol.likes }}</span>
        </div>
        <div class="stat-item">
          <span class="stat-label">Rating</span>
          <span class="stat-value">{{ likeRatio }}%</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'

export default {
  name: 'KolPurchaseCard',
  props: {
    kol: {
      type: Object,
      required: true
    },
    hasAccess: {
      type: Boolean,
      default: false
    }
  },
  emits: ['purchase'],
  setup(props) {
    const priceClass = computed(() => {
      return props.kol.price === 0 ? 'free' : 'paid'
    })

    const likeRatio = computed(() => {
      const total = props.kol.likes + props.kol.unlikes
      if (total === 0) return 0
      return Math.round((props.kol.likes / total) * 100)
    })

    const formatPrice = (lamports) => {
      if (lamports === 0) return 'Free'
      return `${(lamports / 1e9).toFixed(3)} SOL`
    }

    return {
      priceClass,
      likeRatio,
      formatPrice
    }
  }
}
</script>

